export default {

  onLoad (options) {
    let orderNumber = options.orderNumber
    this.getOrderDetail(orderNumber)
  },
  methods: {
    getOrderDetail (orderNumber) {
      this.$request({
        url: '/api/public/v1/my/orders/chkOrder',
        method: 'POST',
        isAuth: true,
        data: {
          order_number: orderNumber
        }
      }).then(res => {
        console.log(res)
      })
    }
  }
}
